
import { defineComponent, reactive } from "vue";
import FallAnimationComponent from "../components/FallAnimation.vue";

export default defineComponent({
  name: "HomeView",
  components: {
    FallAnimationComponent,
  },
  setup() {
    const text = reactive({
      id: 1,
      change() {
        this.id++;
      },
    });

    return {
      text,
    };
  },
});
